<template>
  <Loader v-if="loader"/>
  <div class="home" v-else>
    <span class="caption_change__one">Текущие показатели</span>
    <div class="main" >
      <div class="main__block">
        <span class="main__caption">Текущий вес</span>
        <span class="main__text">{{this.records.weight}} kg</span>
      </div>
      <div class="main__block">
        <span class="main__caption">Желаемый вес</span>
        <span class="main__text">{{this.otherData[1].wishWeight}} kg</span>
      </div>
    </div>
    <div class="weight_index">
      <span class="caption_change__one">Индекс массы тела</span>
      <p
          v-if="this.records.weight"
          class="main__text"
          :class="{index__blue: getITM < 18.5,
                  index__green: (getITM > 18.5) && (getITM <=25),
                  index__red: getITM > 25}"
      >
        {{ getITM }}
      </p>
      <div>
        <span class="text">Рост: {{this.otherData[0].height}} см</span>
        <span class="text">Вес: {{this.records.weight}} кг</span>
      </div>
      <div class="meter">
        <div class="meter__blue" :class="{index: getITM < 18.5}">Недостаточный вес</div>
        <div class="meter__green" :class="{index: (getITM > 18.5) && (getITM <=25)}">Нормальный вес</div>
        <div class="meter__red" :class="{index: (getITM > 25)}">Избыточный вес</div>
      </div>
    </div>
  </div>
</template>

<script>

import Loader from "../components/Loader/Loader";
export default {
  name: 'Home',
  components: {
    Loader
  },
  data: () => ({
    records: [],
    otherData: [],
    loader: true
  }),
  computed: {
    getITM() {
      // console.log(this.getData)
      let height = Number(this.otherData[0].height) / 100
      return (Number(this.records.weight) / Math.pow(height, 2)).toFixed(2)
    }
  },
  async mounted() {
    try {
      // setTimeout(() => {this.loader = true}, 1500)
      this.otherData = await this.$store.dispatch('getOtherFromDatabase')
      const record = await this.$store.dispatch('getValueFromDatabase')

      this.records = record.map(rec => {
        return {
          ...rec
        }
      }).reverse()[0]

      // this.otherData = otherData
      this.loader = false
    } catch (e) {

    }
  }
}
</script>

